@font-face {
  font-family: 'raleway-light';
  src: url("../fonts/raleway-light-webfont.eot");
  /* IE9 Compat Modes */
  src: url("../fonts/raleway-light-webfont.eot?#iefix") format("embedded-opentype"), url("../fonts/raleway-light-webfont.woff2") format("woff2"), url("../fonts/raleway-light-webfont.woff") format("woff"), url("../fonts/raleway-light-webfont.ttf") format("truetype"), url("../fonts/raleway-light-webfont.svg#svgFontName") format("svg");
  /* Legacy iOS */
}

@font-face {
  font-family: 'sans-serif';
}

.promo-bio {
  padding: 0;
}

.promo-bio .promo-bio-content img {
  width: 100%;
  height: auto;
}

.bio-content .gradient-red {
  margin: 0 0 50px 0;
  padding: 10px 0 10px 0;
  background-color: red;
  background-color: #e00a0a;
  background-image: -webkit-radial-gradient(#e00a0a, #8e0606);
  background-image: radial-gradient(  #e00a0a, #8e0606);
}

@media handheld, only screen and (max-width: 768px) {
  .bio-content .gradient-red {
    padding: 5px 0 5px 0;
    margin: 0 0 20px 0;
  }
}

.bio-content .gradient-red h1 {
  color: white;
}

@media handheld, only screen and (max-width: 768px) {
  .bio-content .gradient-red h1 {
    font-size: 18px;
    text-align: center;
    text-transform: capitalize;
  }
}

.bio-content h1 {
  margin: 25px 0 25px 0;
}

.bio-content strong {
  font-size: 18px;
}

.bio-content p {
  font-size: 16px;
}

.no-cssgradients .gradient-red {
  background: url(../img/gradient-red.jpg) repeat-x;
}

.bio-gallery {
  margin-top: 50px;
}

.grid-item {
  width: 33%;
  padding: 5px;
}

.grid-item--width2 {
  width: 400px;
}
